.about{
  height:100%;
  width:100%;
}

.grid {
  background: url("backgroundPic.png");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
}

.grid h5 {
  font-family: "Montserrat", sans-serif;
  font-size: 30px;
  font-weight: bolder;
  color: white;
  margin-top: 2%;
  text-align: center;
}

.grid .grid-items {
  margin-top: 2%;
  display: grid;
  grid-template-columns: 1fr, 1fr, 1fr, 1fr;
  grid-template-rows: 1fr, 1fr, 1fr, 1fr, 1fr, 1fr, 1fr, 1fr, 1fr;
  /* grid-auto-rows: 1fr;
  grid-row-gap: 30px; */
  grid-column-gap: 0px; 
}

.grid .grid-items div {
  /* margin: 40px ; */
  padding: 20px;
  /* text-align: left; */
}

.grid .grid-items div img {
  object-fit: contain;
  border-radius: 30%;
  margin-bottom: 0%;
}

.item--1ET {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 5;
}

.item--2ET_d {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 5;
}

.item--3SK {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 3;
}

.item--4SK_d {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 3;
}

.item--5PK {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 5;
  grid-row-end: 9;
}

.item--6PK_d {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 5;
  grid-row-end: 9;
}

.item--7YL {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 3;
  grid-row-end: 6;
}

.item--8YL_d {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 3;
  grid-row-end: 6;
}

.item--9YD {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 6;
  grid-row-end: 9;
}

.item--10YD_d {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 6;
  grid-row-end: 9;
}

